@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&display=swap);
*{padding:0;margin:0;box-sizing:border-box}html,body{height:100vh;width:100%;font-family:"Montserrat", sans-serif;font-size:100%}.main-frame{width:100%;height:100vh;padding:3.5em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;grid-gap:2rem;gap:2rem;background-color:#fefefe;font-size:1rem;color:#070707}.main-frame #new-quote{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;grid-gap:1.5rem;gap:1.5rem;min-height:50%;max-width:400px;padding:1em}.main-frame #new-quote #quote-box{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:flex-start;align-items:flex-start;grid-gap:0.5rem;gap:0.5rem;font-size:1.8rem;cursor:pointer}.main-frame #new-quote #quote-box #author{width:100%;font-size:1.4rem;font-weight:200}.main-frame #new-quote #quote-box #author::before{content:"\2014 "}.main-frame #new-quote #quote-box #watermark{letter-spacing:10px;font-weight:200;font-size:1rem}.main-frame #new-quote #quote-box ::selection{background:none}.main-frame #controls-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;grid-gap:2rem;gap:2rem}.icon{text-decoration:none;font-size:1.4rem;color:#070707;outline:none;transition:all 500ms ease-in-out}.icon:hover{cursor:pointer;color:#2522ca}

